<template>
  <header id="header">
    <nav :class="{ navbackground: (this.$route.path !== '/')}">
      <div class="nav-wrapper">
        <!-- <a href="#" class="brand-logo center logo">Sky的精神角落</a> -->
        <ul id="nav-mobile" class="right hide-on-med-and-down">
          <li class="toHome" @click="goNext('home')">
            <a>Home</a>
          </li>
          <li class="toArticles" @click="goNext('article')">
            <a>Articles</a>
          </li>
          <li class="toProjects" @click="goNext('project')">
            <a>Projects</a>
          </li>
        </ul>
        <button
          class="sidenav-trigger waves-effect"
          @click="sidenavShow=!sidenavShow"
          :class="{'sidenavTriggerActive':sidenavShow}"
        >
          <span></span>
        </button>
      </div>
    </nav>
    <transition name="sidenav">
      <ul class="sidenav" v-if="sidenavShow" @click="sidenavShow=!sidenavShow">
        <li class="toHome">
          <a class="waves-effect" @click="goNext('home')">Home</a>
        </li>
        <li class="toArticles">
          <a class="waves-effect" @click="goNext('article')">Articles</a>
        </li>
        <li class="toProjects">
          <a class="waves-effect" @click="goNext('project')">Projects</a>
        </li>
        <li class>
          <a class="waves-effect">About me</a>
        </li>
      </ul>
    </transition>
  </header>
</template>

<script>
import $ from 'jquery'
export default {
  data() {
    return {
      sidenavShow: false
    }
  },
  methods: {
    scroll() {
      if (this.$route.path == '/') {
        if ($(window).scrollTop() > $(window).height() - $('nav').height()) {
          $('nav').addClass('navbackground')
        } else {
          $('nav').removeClass('navbackground')
        }
      }
    }
  },
  mounted() {
    window.onscroll = this.scroll
  }
}
</script>

<style lang="scss">
.sidenav-enter-active {
  animation: sidenav 0.4s forwards;
}
.sidenav-leave-active {
  animation: sidenav 0.4s reverse forwards;
}
@keyframes sidenav {
  0% {
    transform: translateX(-105%);
  }
  30% {
    transform: translateX(30%);
  }
  100% {
    transform: translateX(0);
  }
}
@media only screen and (min-width: 992px) {
  #header {
    .sidenav-trigger {
      display: none;
    }
  }
}
#header {
  nav {
    position: fixed;
    box-shadow: none;
    background-color: transparent;
    z-index: 1000;
    a i {
      font-size: 1.5em;
    }
  }
  .sidenav-trigger {
    width: 50px;
    height: 60px;
    position: relative;
    background: transparent;
    border: none;
    vertical-align: middle;
    padding: 18px 12px;
    margin: 0;
    cursor: pointer;
    &:focus {
      outline: 0;
    }
    &:hover {
      span,
      span:before,
      span:after {
        background: #880665;
      }
    }
    &:before {
      content: '';
      opacity: 0;
      width: 0;
      height: 0;
      border-radius: 50%;
      position: absolute;
      top: 50%;
      left: 50%;
      background: transparent;
      transform: translate(-50%, -50%);
      transition: all 0.4s ease;
    }
    span {
      display: block;
      position: relative;
      &:before,
      &:after {
        content: '';
        position: absolute;
        left: 0;
      }
      &:before {
        top: -8px;
      }
      &:after {
        bottom: -8px;
      }
    }
    span,
    span:before,
    span:after {
      width: 100%;
      height: 4px;
      background: #f300b4;
      transition: all 0.4s ease;
    }
    &.sidenavTriggerActive {
      &:before {
        opacity: 1;
        width: 33px;
        height: 33px;
        background: #fff;
      }
      span {
        background: transparent;
        &:before {
          top: 0;
          transform: rotate(225deg);
        }
        &:after {
          bottom: 0;
          transform: rotate(-225deg);
        }
      }
    }
  }
  .sidenav {
    margin-top: 56px;
    height: 200px;
    width: 100%;
    z-index: 1001;
    padding-bottom: 0; // 重置materialize的默认60px下内边距
    background-color: #181d23;
    transform: translateX(0);
    overflow: hidden;
    li {
      &:hover {
        background-color: rgb(54, 53, 53);
      }
      a {
        text-align: center;
        color: #fff;
      }
    }
  }
  .navbackground {
    background-color: #181d23 !important;
    box-shadow: 0px 2px 3px #888888 !important;
  }
}
</style>



